<template>
	<view>
		<view class="my">
		  <view class="head height">
		    <view class="user_img" @click="jump">
					<image :src="user_img" mode=""></image>
				 </view>
		    <view class="user_name">
		      {{user_name || '昵称' }}
		    </view>
		    <view class="user_id">
		      ID：{{ userMsg.user_id || '暂无' }}
		    </view>
		  </view>
		
		  <view class="content height">
		    <view class="list">
		      <view 
						v-for="(item,index) in useList" 
						:key="index"
						@click="open(index)"
						class="list_item" 
					>
		        <view class="left">
		          <view class="left_icon">
		             <image :src=item.icon mode=""></image>
		          </view>
		          <view>
		            {{item.txt}}
		          </view>
		        </view>
		        <view class="right">
		          <image v-if="index!=1" src="/static/right.png" mode=""></image>
		          <view class="tic" v-else>
								{{tic_num}}票
							</view>
		        </view>
		      </view>
		    </view>
			</view>
			
			<!-- 分类展示头 -->
			<swiperTabHead  :tabBars="tabBars" :tabIndex="tabIndex" @tabtap="tabtap"></swiperTabHead>
	
			
			<swiper :style="{height:swiper_height}" :current="tabIndex" @change="tabChange">
				<swiper-item style="box-sizing: border-box;padding: 0 10rpx;" v-for="(tab,i) in tabBars" :key="i">
					<mescroll-item :waterFullHeight="swiper_height" :i="i" :index="tabIndex" :tabs="tabBars"></mescroll-item>
				</swiper-item>
			</swiper>
			
			
		</view>
		<!-- 个人资料填写的弹出框 -->
		<uni-popup class="pop" animation="false" ref="popup_user" type="center" mask-click="false">
			<uni-popup-usermsg type="success"  message="成功消息" duration="0" />
			<view class="imgBox">
				<image class="img" src="/static/close.png" mode="" @click="close"></image>
			</view>
		</uni-popup>
		
	</view>
</template>

<script>
	import MescrollItem from "@/components/mescroll-swiper-item.vue";
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	// import {apiSearch} from "@/API/mock.js"
	
	// 上传个人资料的弹框
	import uniPopupUsermsg from '@/components/uni-popup/uni-popup-usermsg.vue'
	
	// 引入tabHead 切换
	import swiperTabHead from "@/components/swiper-tab-head.vue";
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				// 测试
				swiper_height: "", // 需要固定swiper的高度
				tabIndex: 0, // 当前tab的下标
				//控制nav切换
				tabIndex:0,// 选中的
				tabBars:[
					{ name:"收藏",id:"guanzhu" },
					{ name:"赞过",id:"tuijian" },
				],   
				// 个人展示
				act:0,
				useList:[
					{icon:"/static/my_use.png",txt:'个人资料'},
					{icon:"/static/my_ticket.png",txt:'剩余票数（每日限投3票）'},
					{icon:"/static/my_exit1.png",txt:'退出登录'}
				],
				tic_num:3,
				// 用户信息显示
				user_img:'', //用户头像
				user_name:'',//用户名
			};
		},
		components:{
			swiperTabHead,
			MescrollItem,
			uniPopupUsermsg
		},
		onLoad(){
			this.swiper_height = uni.getSystemInfoSync().windowHeight + 'px'
			let _this = this
			console.log('我的页面')
		
			// this.$refs.popup_video.open()	
			// this.$refs.popup_user.open()
			uni.showLoading({
			   title: '加载中'
			});
		},
		onReady() {
			// 页面加载完毕
			console.log('页面加载完毕')
			uni.hideLoading();
		},
		onShow() {
			console.log('onshow')
			// 获取用户名
			this.user_name = uni.getStorageSync('user_name')
			// 获取头像
			this.user_img = uni.getStorageSync('user_img')
		},
		methods:{
			//滑动切换导航
			tabChange(e){
			  this.tabIndex = e.detail.current
			},
			//接受子组件传过来的值点击切换导航
			tabtap(index){
				this.tabIndex = index;
			},
			// 制作瀑布流
			getLoadNum(num){
			    console.log('共加载了:'+num);
			    !this.isNewRenderDone&&uni.hideLoading()
			    this.isNewRenderDone = true
			},	
			// 点击我的视频跳转到视频播放页
			goPlayVideo(){
				uni.navigateTo({
					url: "/pages/playVideo/playVideo"
				})
			},
			// 进行弹窗的控制
			open(num){
				let _this = this 
				if(num === 9){
				// 显示上传视频
					this.$refs.popup_video.open()
				}else if(num === 0){
					// 显示个人资料
					this.$refs.popup_user.open()
				}else if(num === 2){
					// 显示退出登录
					uni.showModal({
						title:"提示",
						content:"确定退出吗",
						success:function(res){
							if (res.confirm) {
								console.log('用户点击确定');
								// 用户确认退出登录，删除本地用户登录的信息
									// 删除内部的值
									_this.user_name = ''
									_this.user_img = ''
									// 删除本地缓存
									uni.removeStorageSync('user_name')
									uni.removeStorageSync('user_img')
								
							} else if (res.cancel) {
								console.log('用户点击取消');
								// 不进行操作弹出框取消即可
								
							}
						},
						fail:function() {
							console.log('调用接口失败')
						}
					})
				}
			},
			close(num){
				// console.log('关闭')
				this.$refs.popup_user.close()
				this.$refs.popup_video.close()
			},
			
			// 获取用户的信息  暂时用不到，
			// 逻辑：跳转到手机登录页面，进行登录然后将返回的信息进行本地存储，在my 页面再次显示时，获取信息
			getUserMsg(){
				let _this = this
				// 获取用户信息
				uni.getUserInfo({
				    // 获取信息成功
				    success(res) {
				        console.log('请求成功',res);
								// 获取个人昵称、头像、ID
								let userMsg = {
									userName:res.userInfo.nickName,
									userImg:res.userInfo.avatarUrl
								}
								_this.userMsg = userMsg
								// 进行本地存储
								uni.setStorage({
								    key: 'userMsg',
								    data: JSON.stringify(userMsg),
								    success: function () {
								        console.log('success');
								    }
								});
				        // 成功后进行登录,获取code
				        uni.login({
				          success (res) {
				             console.log('成功登录',res);
				            if (res.code) {
				              //发起网络请求 请求个人信息
				              // uni.request({
				              //   // 请求路径
				              //   url: 'https://test.com/onLogin',  
				              //   // 请求参数code
				              //   data: {
				              //     code: res.code
				              //   },
				              //   method: 'GET',
				              //   success(res){
				              //       // 请求成功后获取openid和session_key
				              //       console.log(res)
				              //   }
				              // })
				            } else {
				              console.log('登录失败！' + res.errMsg)
				            }
				          }
				        })
				    },
				    fail() {
				        console.log("获取用户信息失败");
				    }
				})
			},
		
			// 跳转到注册页
			jump(){
				console.log('跳转页面')
				uni.navigateTo({
				    url: "/pages/login/login"
				});
			},
		}
	}
</script>

<style lang="less">
	
	
	
	.pop{
		.imgBox{
			width: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			.img{
				width: 62.5rpx;
				height: 62.5rpx;
				margin-top: 20rpx;
			}
		}
	}
	.my{
	  background-color: #f6f6f6;
	  box-sizing: border-box;
		color: black;
	  .head{
	    color: white;
	    font-size: 30rpx;
	    display: flex;
	    align-items: center;
	    flex-direction: column;
			box-sizing: border-box;
	    padding-top: 64rpx;
	    // 花个半圆
	    width: 750rpx;
	    height: 310.47rpx;
	    background-image: linear-gradient(to right, #3d1c9e , #7255a5);
	    border-radius:0 0 750rpx 750rpx ;
	    .user_img{
	      width: 120rpx;
	      height: 120rpx;
	      background-color: white;
	      border-radius: 50%;
				image{
					width: 100%;
					height: 100%;
					border-radius: 50%;
				}
	    }
	    .user_name{
	      margin-top: 20rpx;
	    }
	  }
	  .content{
	    box-sizing: border-box;
	    padding: 40rpx;
			padding-bottom: 10rpx;
			font-size: 30rpx;
	    .list{
	      width: 100%;
	      height: 218rpx;
	      background-color: white;
	      border-radius: 20rpx;
	      box-sizing: border-box;
	      padding: 20rpx 45.83rpx;
	      color: #707070;
				
	      .list_item{
					position: relative;
					box-sizing: border-box;
	        display: flex;
	        justify-content: space-between;
	        align-items: center;
	        height: 58rpx;
	        .left{
	          display: flex;
	          justify-content: space-between;
	          align-items: center;
						.left_icon{
							width: 38rpx;
							height: 32rpx;
							margin-right: 20rpx;
							image{
								width: 100%;
								height: 100%;
							}
						}
	        }
					.right{
						width:46.66rpx ;
						height: 37.16rpx;
						.tic{
							width: 48rpx;
							padding-right: 20rpx;
						}
						image{
							width: 100%;
							height: 100%;
						}
					}
				}
			}
	    .title{
	      display: flex;
	      justify-content: start;
				align-items: center;
	      margin: 39rpx 20rpx;
				.title_icon{
					width: 40.77rpx;
					height: 44.77rpx;
					margin-right: 16rpx;
					image{
						width: 100%;
						height: 100%;
					}
				}
	    }
	    .video_list{
	      width: 100%;
	      display: flex;
	      justify-content: space-between;
	      flex-wrap: wrap;
	      .video_item{
	        width:314rpx;
	        height:308rpx ;
	        background-color: white;
	        border-radius: 20rpx;
	        box-sizing: border-box;
	        padding-top: 25rpx;
	        .video_box{
	          height: 227rpx;
	          width: 100%;
						#myVideo{
							height: 227rpx;
							width:314rpx;
						}
	        }
	        .msg{
	          display: flex;
	          justify-content: start;
	          font-size: 20rpx;
	          box-sizing: border-box;
	          padding-left: 20rpx;
	          margin-top: 10rpx;
	          color: black;
	          .see{
	            margin-right: 20rpx;
							display: flex;
							justify-content: start;
							align-items: center;
							.see_icon{
								width: 40.77rpx;
								height: 44.77rpx;
								margin-right: 16rpx;
								image{
									width: 100%;
									height: 100%;
								}
							}
	          }
						.say{
							margin-right: 20rpx;
							display: flex;
							justify-content: start;
							align-items: center;
							.say_icon{
								width: 40.77rpx;
								height: 35.77rpx;
								margin-right: 16rpx;
								image{
									width: 100%;
									height: 100%;
								}
							}
						}
	        }
				}
	    }
		}
	}
</style>
